'use strict';
import { getStorage, setStorage } from '@/utils/storage.js';
var _self;
export default {
	data() {
		return{
			index:0
		}
	},
	methods:{
		swapRole(e){
			_self.index=e
			setStorage('index', e)
		}
	},
	async onLoad() {
		_self = this
		_self.index=getStorage('index')
	}
};
